import React,{Suspense,lazy} from 'react'
import Loading from "../../../components/shared-components/Loading";
import {Route, Switch} from "react-router-dom";

const VillaViews = ({match})=> {
    return (
        <Suspense fallback={<Loading cover={'page'}/>}>
            <Switch>
                <Route path={`${ match.url }/list`} component={lazy(()=> import("./villa-list/index"))}/>
                <Route path={`${ match.url }/add`} component={lazy(()=> import("./villa-add/index"))}/>
                <Route path={`${ match.url }/p/:id`} exact component={lazy(()=> import("./villa-detail/index"))}/>
                <Route path={`${match.url}/p/:id/transaction`} component={lazy(()=> import("./villa-detail/transaction-history/index"))}/>
                <Route path={`${match.url}/p/:id/rooms`} component={lazy(()=> import("./villa-detail/detail-rooms/index"))}/>
                <Route component={lazy(()=> import("../../auth-views/errors/error-page-1/index"))}/>
            </Switch>
        </Suspense>
    )
}

export default VillaViews
